@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  color: rgb(18, 18, 18);
}

body {
  background-color: rgb(255, 255, 255);
}
body div.popup {
  position: fixed;
  top: 375px;
  left: 260px;
  width: 920px;
  height: 210px;
  border-width: 1px;
  border-style: solid;
  background-color: rgb(255, 255, 255);
  z-index: 999;
  visibility: hidden;
}
body div.popup button#popup-close-button {
  border: none;
  width: 24px;
  height: 24px;
  background-color: rgb(255, 255, 255);
  font-size: 24px;
  font-weight: 700;
  position: relative;
  left: 856px;
  top: 46px;
}
body div.popup header {
  font-size: 42px;
  font-weight: 700;
  width: 380px;
  height: 50px;
  margin-top: 56px;
  margin-left: 95px;
  position: relative;
  bottom: 22px;
}
body div.popup p {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  margin-top: 24px;
  margin-left: 95px;
  width: 380px;
  position: relative;
  bottom: 22px;
}
body main section {
  display: flex;
  margin-top: 96px;
  width: 1440px;
}
body main section img {
  width: 505px;
  height: 330px;
  margin-left: 230px;
}
body main section article {
  width: 380px;
  margin-left: 95px;
}
body main section article header {
  font-size: 42px;
  font-weight: 700;
  height: 50px;
  margin-top: 40px;
}
body main section article p {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  margin-top: 24px;
  margin-bottom: 32px;
}
body main section article button {
  font-size: 16px;
  font-weight: 700;
  background-color: rgb(228, 193, 174);
  border: none;
  border-radius: 32px;
  color: #FFFFFF;
  width: 128px;
  height: 48px;
}
body main section article button:hover {
  cursor: pointer;
}